import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { GlobalProvider } from '../../providers/global/global';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
usr ='';pwd='';
  constructor(public navCtrl: NavController, public glbl:GlobalProvider) {
    this.getdata();
    this.postsingledat();
    
  }

  getdata()
  {
    this.glbl.getfun('company.php').subscribe(data=>{
      console.log(data);    
    });
    
  }
  postsingledat()
  {
    let postData = new FormData();
    postData.append('linkId' , '33');
    this.glbl.postfun('getSections.php', postData).subscribe(data=>{
      console.log(data);
    });
  }
  postmultipledat()
  {
    let arr = ['usr','pwd'];
    let val = [this.usr, this.pwd];
    let postData = new FormData();
    for(let i =0; i < arr.length; i++)
    {
      postData.append(arr[i], val[i]);
    }
    this.glbl.postfun('loginbuilder.php', postData).subscribe(data=>{
      console.log(data);
    });
  }
}



