import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {Observable} from "rxjs/Observable";
import 'rxjs/add/operator/map';
/*
  Generated class for the GlobalProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class GlobalProvider {
  url = 'https://sapphireprofits.com/siteapi/';
  constructor(public http: HttpClient) {
    console.log('Hello GlobalProvider Provider');
  }

  getfun(api):Observable<any>
  {
    return this.http.get(this.url+api);
  }
  postfun(api, param):Observable<any>
  {
    
    return this.http.post(this.url+api, param);
    // this.http.post('https://sapphireprofits.com/siteapi/getSections.php', postData)
    // .subscribe(data => {
    //     console.log(data);
    //  },
    //  err => {
    //       console.log('Error: ' + err.error);
    //   });
    
  }
}



